export default {
	props:{
		resdata:{},
		height:{
			type:String,
			default:"350rpx"
		},
		preview:{
			type:Boolean,
			dafault:false
		}
	},
	computed:{
		getStyle(){
			return `height: ${this.height}rpx`
		},
		getUrls(){
			var urls = []
			for (let v in this.resdata) {
				urls.push(this.resdata[v].src)
			}
			return urls
		}
	},
	methods:{
		event(item,index){
			if(this.preview) {
				return uni.previewImage({
					current:index,
					urls: this.getUrls,
					indicator:"default"
				})
			}
		}
	}
}
